<template>
    <div>
        <h1>Projects</h1>
        <el-row>
            <el-col :span="4">
                <div>
                    <h2>Start – End Date</h2>
                </div>
            </el-col>
            <el-col :span="20">
                <div class="pad">
                    <h2>Informatione</h2>
                </div>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="4">
                <div class="dis" v-for="(item, index)  in  items" :key="item.message"
                    :style="{ height: videoHeight[index] + 'px' }">
                    <p>{{ item.data }}</p>
                </div>
            </el-col>
            <el-col :span="20" class="bottom">
                <div class="dis" id="video" ref="video" v-for="item in  items" :key="item.message">
                    <ol v-for=" k in item.message" :key="k.title" :index="k.title">
                        <span>
                            {{ k.title.substring(0, k.title.indexOf('Project Title:')) }}<b>Project Title:</b>{{
                                k.title.substring(k.title.indexOf('Project Title:') + 14) }}
                        </span>
                        <br />
                        <span>
                            {{ k.role.substring(0, k.role.indexOf('Role:')) }}<b>Role:</b>{{
                                k.role.substring(k.role.indexOf('Role:') + 5) }}
                        </span>
                        <br />
                        <span>
                            {{ k.funder.substring(0, k.funder.indexOf('Funder:')) }}<b>Funder:</b>{{
                                k.funder.substring(k.funder.indexOf('Funder:') + 7) }}
                        </span>
                    </ol>
                </div>
            </el-col>
        </el-row>
    </div>
</template>
  
<script>
export default {
    data() {
        var pub = new Array
        var oA = document.createElement("a") //创建a标签

        return {
            videoHeight: [96, 72, 96, 72, 96, 96, 120],
            items: [
                {
                    data: '2019/12 – 2021/04',
                    message: [
                        {
                            title: 'Project Title: Next-Generation V2X Network Architecture and Ecosystem for Smart Mobility (COSMO)',
                            role: 'Role: Research Fellow',
                            funder: 'Funder: A*Star (Grant No. A19D6a0053)',
                        }
                    ],

                },
                {
                    data: '2018/06 – 2019/12',
                    message: [{
                        title: 'Project Title: A Cyber-Physical Approach to Securing Urban Transportation Systems',
                        role: 'Role: Research Fellow',
                        funder: 'Funder: National Research Foundation Singapore (Grant No. NRF2014-NCR-NCR001-040)',
                    }
                    ],

                },
                {
                    data: '2019/01 – 2022/12',
                    message: [{
                        title: 'Project Title: Research on Key cryptographic Technology for Privacy-preserving distributed multimedia big data',
                        role: 'Role: Principal Investigator (PI)',
                        funder: 'Funder: Nature Science Foundation of Chongqing (Grant No. CSTB2022NSCQ-MSX0437)',
                    }
                    ],

                },
                {
                    data: '2019/01 – 2022/12',
                    message: [{
                        title: 'Project Title: Towards the Cryptographic Techniques for Privacy-preserving Indoor Location',
                        role: 'Role: Principal Investigator (PI)',
                        funder: 'Funder: Nature Science Foundation of China (Grant No. 61872051)',
                    }
                    ],

                },
                {
                    data: '2017/01 – 2017/12',
                    message: [{
                        title: 'Project Title: Indistinguishability-based Provably Secure Authenticated Key Exchange in a Quantum World',
                        role: 'Role: Principal Investigator (PI)',
                        funder: 'Funder: Nature Science Foundation of China (Grant No. 11647097)',
                    }
                    ],

                },
                {
                    data: '2017/01 – 2019/12',
                    message: [{
                        title: 'Project Title: Privacy Preserving Library Data Retrieval in Big Data Era',
                        role: 'Role: Principal Investigator (PI)',
                        funder: 'Funder: Scientific and Technological Research Program of Ministry of Education of China (Grant No. 16YJC870018)',
                    }
                    ],

                },
                {
                    data: '2015/07 – 2017/06',
                    message: [{
                        title: 'Project Title: On strong security model and construction for dynamic group authenticated key exchange protocol',
                        role: 'Role: Principal Investigator (PI)',
                        funder: 'Funder: Scientific and Technological Research Program of Chongqing Municipal Education Commission (Grant No. KJ1500918)',
                    }
                    ],

                },


            ],
        }

    },
    methods: {
        getVideoHeight() {

            //console.log('0qqqqqqqqqqqqqqqqqqqqq', this.$refs.video)

            this.video = this.$refs.video
            console.log('0qqqqqqqqqqqqqqqqqqqqq', this.video.length)
            for (var i = 0; i < this.video.length; i++) {
                this.videoHeight[i] = this.video[i].offsetHeight;
                console.log(this.videoHeight[i])
            }

            //videoHeight = this.$refs.video.offsetHeight + 'px'
            //this.videoHeight = 96+'px'
            //console.log('1qqqqqqqqqqqqqqqqqqqqq')
        }
    },
    mounted() {
        this.$nextTick(() => {
            // console.log('2qqqqqqqqqqqqqqqqqqqq')
            this.getVideoHeight();
        })
    },
    created() {
        // console.log('3qqqqqqqqqqqqqqqqqqqqq')
        window.addEventListener('loadedmetadata', this.getVideoHeight)
    }

}


</script>
<style>
h1 {
    margin-bottom: 10px;
    margin-top: 10px;
    font-size: 1.563em;
}

ol {
    font-size: 16px;
    line-height: 1.5;
    margin: 0px;
}

h2 {
    font-size: 18px;
    line-height: 1.5;
    margin-top: 0px;
    margin-bottom: 9px;
}

.pad {
    padding-left: 40px;
}

span {
    display: inline-block;
    font-size: 16px;
    line-height: 1.5;
}

p {
    font-size: 16px;
    line-height: 1.5;
    margin: 0px;
}

.dis {
    margin-bottom: 10px;
}</style>




